<template>
  <div id="DocView">
    <!-- doc content -->
    <div class="doc-content">
      <Hero title="Classes" desc="Object Orientation in Skef" />
      <Section>
        Skef provides total support for Object Oriented Programming, the syntax
        is pretty simplified, allowing a good comprehension of the code. The OOP
        of Skef is based on the traditional notations of object orientation,
        including objects, inheritance, polimorphism, and so on. Classes in Skef
        provides a simple and clear way to creating objects and instancing them.
      </Section>
      <hr style="background-color: #66666612" />
      <Section anchor="basics">
        <a slot="title">Basics</a>

        Let's start with the basics notation of classes in Skef.
        <br />
        Skef has builtin classes that provide the Type Handling. Introducing it,
        we have 5 builtin types, that act like classes: <b>num</b>, <b>str</b>, <b>bool</b>,
        <b>list</b> and <b>dict</b>, we will focus on these types on
        <router-link to="/docs/types">Types</router-link> section. <br /> <br />
        There are two ways to instance a variable with these types:
        <pre class="language-py"><code>foo = num()
bar = str()
baz = list()
</code></pre>
        or simply:
        <pre class="language-py"><code>foo = 123 <br />
bar = 'abc'
baz = [1,2,3]
</code></pre>
      </Section>
      <hr style="background-color: #66666612" />
      <Section anchor="builtins">
        <a slot="title">Builtin Functions</a>
        Skef allow you to call class methods with some sort of builtin
        functions: <br />
        <b>len, sort, sorted, get, set, help, doc, str, int.</b> <br />
        They are called by their inner methods. len calls the class method <b>@len</b>, sort call <b>@sort</b> and so on <br />
        <br />
        For example, if you want to get the length of an object:

        <pre class="language-py"><code>some_str = str('abc') # you can also the string instance with 'abc'
str_len = len(some_str) # get the length of the object

some_dict = { 'first name': 'John', 'last name': 'Doe' } # instancing a new dict
dict_len = len(some_dict) # getting the dict length

print str_len, dict_len   # output: [ 3, 2 ]
</code></pre>
        You can also call these functions by calling the class inner method
        <pre class="language-py"
        ><code>some_str = str('abc') # you can also the string instance with 'abc'
str_len = some_str.@len() # get the length of the object

some_dict = { 'first name': 'John', 'last name': 'Doe' } # instancing a new dict
dict_len = some_dict.@len() # getting the dict length

print str_len, dict_len   # output: [ 3, 2 ]
</code></pre>
      So, you can do the same with your own classes!
        <pre
          class="language-py"
        ><code>class MyString {
  # this initialization is not required,
  # just to improve the code organization and the readability
  data = ''

  # create a constructor for MyString
  (str) {
    # initialize data
    self.data = str
  }

  @len() {
    return len(self.data) # self.data.@len also works
  }
}

my_str = MyString('skef') # instancing the class
print len(my_str) # will return 4
</code></pre>
      </Section>
